

const Semantic = (props) => {
    console.table(props.tags);
    return (
        <div>
            {
                props.tags.forEach(arrTag=>(
                    <p>
                        <label>{arrTag[0]} </label> {arrTag[1]}
                    </p>
                ))
            }
        </div>
    );
}

export default Semantic;

